@import "../variables.scss";

body{ 
    // background-image: url('../../fotos/img/back-office.jpg');
   // font-family: 'Roboto', sans-serif;
}

.panel-heading{
    padding-left:0px; 
    background-color: $color-gral;
}
.bloque-exterior{
    padding-bottom: 30px;
}
.bloque {
    padding: 2em 2em;
    background: #999;
}

.bloque h4 {
    font-size: 1.7em;
    color: #fff;
    // margin: 0.3em 0em;
    // font-family: 'Carrois Gothic', sans-serif;
}

.bloque p {
    color: #fff;
    font-size: 0.8em;
    line-height: 1.8em;
}

.bloque.tipo-1 {
    background: $color-gral;
    margin-right: 0.8em;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    transition: 0.3s all;
    -webkit-transition: 0.3s all;
    -moz-transition: 0.3s all;
    -o-transition: 0.3s all;
    border-radius: 5px 20px 5px;
}


.bloque.tipo-1:hover {
    background: darken($color-gral,10%);
    transition: 0.5s all;
    -webkit-transition: 0.5s all;
    -moz-transition: 0.5s all;
    -o-transition: 0.5s all;
    // cursor:hand;
}

.update-right i.fa.fa-users,i.fa.fa-home,i.fa.fa-calendar,i.fa.fa-building {
    font-size: 3em;
    color: $color-gral;
    width: 80px;
    height: 80px;
    background: #fff;
    text-align: center;
    border-radius: 49px;
    -webkit-border-radius: 49px;
    -moz-border-radius: 49px;
    -o-border-radius: 49px;
    line-height: 1.7em;
}


.update-left {
    padding: 0px;
}

.cont-btn{
    padding-top: 30px;
}


.panel-heading{

    background-color:lighten($color-gral,20%) !important;    
    color:white !important;

}

